@import url(https://fonts.googleapis.com/css?family=Eczar&display=swap);
@import url(https://fonts.googleapis.com/css?family=Source+Code+Pro&display=swap);
@import url(https://fonts.googleapis.com/css?family=Kanit&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.projects-each {
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin: 20px; 
    box-shadow: 0 10px 5px #888888;
    background-color: #F9F871;
}
.image-wrapper img {
    display: flex;
    width: 500px;
    height: auto;
    padding: 15px;
    margin-top: 20px;
}
.project-info {
    display: flex;
    flex-direction: column;
    width: 400px;
    height: auto;
    padding: 15px;
    font-size: 20px;
    font-family: 'Kanit', sans-serif;
    color: black;
}
.projects>div {
    flex: 0 1 50%;
    box-sizing:border-box;
    display: flex;
    justify-content: space-evenly;
}
.project-title{
    display: flex;
    justify-content: center;
    margin-bottom: 20px;
    font-weight: bold;
    font-size: 30px;
}
.project-link a{
    background-color: #F9F871;
    color: black;
    text-decoration: none;
}
.project-title:hover{
    background-color: #555;
    color: white; 
}


html {
    scroll-behavior: smooth;
}
#project-container {
    display: flex;
    width: 298px;
    flex-direction: column;   
}
.projects {
    display: flex;
    justify-content: center;
    margin-top: 10%;
    margin-bottom: 10%;
}
body {
    background-color: #DADDE2;
}

.header {
    background-color:rgb(54, 53, 57);
    height: 145px;
    padding-top: 50px;
    box-shadow: 0 10px 5px #888888;
}
.title{
  position: relative;
  top: 40%;  
  width: 24em;
  color: white;
  margin: 0 auto;
  border-right: 2px solid rgba(255,255,255,.75);
  font-size: 120%;
  text-align: center;
  white-space: nowrap;
  overflow: hidden;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);    
}
.typewriter{
  -webkit-animation: typewriter 2s steps(44) .2s 1 normal both,
             blinkTextCursor 500ms steps(44) infinite normal;
          animation: typewriter 2s steps(44) .2s 1 normal both,
             blinkTextCursor 500ms steps(44) infinite normal;
}
@-webkit-keyframes typewriter{
  from{width: 0;}
  to{width: 24em;}
}
@keyframes typewriter{
  from{width: 0;}
  to{width: 24em;}
}
@-webkit-keyframes blinkTextCursor{
  from{border-right-color: rgba(255,255,255,.75);}
  to{border-right-color: transparent;}
}
@keyframes blinkTextCursor{
  from{border-right-color: rgba(255,255,255,.75);}
  to{border-right-color: transparent;}
}


.navbar {
    display: flex;
    justify-content: space-around;
    padding-bottom: 20px;
    margin-bottom: 0;
    text-decoration: none;
}
a:link {
    text-decoration: none;
  }
a {
    color: white;
    font-weight: bold;
    padding-left: 20px;
    padding-right: 20px;
  }
a:hover {
    background-color: #555;
    color: white;
  }
  
#footer {
    display: flex;
    background-color: rgb(54, 53, 57);
    height: 500px;
    box-shadow: 0 -10px 5px #888888;
}
.contact-form{
    display: flex;
    flex-direction: column;
    padding: 50px;
    width: 100%;
    margin: 50px;
    margin-top: 20px
}

.contact-form input{
    font-size: 20px;
    margin-top: 10px;
    width: auto;
    height: 30px;
}

.contact-form textarea{
    font-size: 20px;
    margin-top: 20px;
    width: auto;
    height: 150px;
}

button {
    background-color: red;
    margin-top: 20px;
    font-size: 20px;
    width: 30%;
    height: auto;
}
.zoom{
    display: flex;
    justify-content: center;
    -webkit-transition: -webkit-transform .2s;
    transition: -webkit-transform .2s;
    transition: transform .2s;
    transition: transform .2s, -webkit-transform .2s;
}
.zoom:hover { /* IE 9 */
    -webkit-transform: scale(1.2); /* Safari 3-8 */
    transform: scale(1.2); 
}
.self-portrait {
    margin: 30px;
    border:white 10px solid;
    border-radius: 50%;
}
img { 
    width: auto;
    height: 300px; 
} 

.contact-links img{
    display: flex;
    justify-content: space-between;
    margin-top: 8%;
    height: 30%;
    width: auto;
    margin-top: 60px;
}

.contact-links img:hover{
    height: 40%;
}

.contact-links {
    justify-content: space-between;
    width: 80px;
    margin-left: 20%;
    padding-bottom: 0;
    margin-top: 40px;
    margin-bottom: 100px;
}

#about-me {
    display: flex;
    flex-direction: column;
    background-color: #F9F871;
    font-size: 20px;
    margin: 5%;
    font-family: 'Kanit', sans-serif;
    padding: 60px;
    box-shadow: 0 10px 5px #888888;
}
#about-me h3 {
    overflow: hidden;
    text-align: center;
    font-family: 'Source Code Pro', monospace;
    font-size: 30px;
    margin-bottom: 5px;
   }
h3:before,
h3:after {
    background-color: black;
    content: "";
    display: inline-block;
    height: 5px;
    position: relative;
    vertical-align: middle;
    width: 20%;
}
h3:before {
    right: 0.5em;
    margin-left: -50%;
}
h3:after {
    left: 0.5em;
    margin-right: -50%;
}

.about-me h3 {
    overflow: hidden;
    text-align: center;
    font-family: 'Source Code Pro', monospace;
    font-size: 40px;
    margin-bottom: 50px;
   }
h3:before,
h3:after {
    background-color: black;
    content: "";
    display: inline-block;
    height: 5px;
    position: relative;
    vertical-align: middle;
    width: 20%;
}
h3:before {
    right: 0.5em;
    margin-left: -50%;
}
h3:after {
    left: 0.5em;
    margin-right: -50%;
}

.about-me-images h3{
    font-family: 'Source Code Pro', monospace;
    font-size: 20px;
    margin: 30px;
    text-align: center;
}

.about-me-images img{
    height: 100px;
    width: auto;
    margin-top: 20px; 
    margin-left: 15px;  
}
